body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body
{
  height: 100%;
}

body
{
  margin: 0px;
  padding: 0px;
  background: #181818;
  font-family: 'Didact Gothic', sans-serif;
  font-size: 12pt;
  font-weight: 200;
  color: #FFF;
}


h1, h2, h3
{
  margin: 0;
  padding: 0;
}

p, ol, ul
{
  margin-top: 0;
}

ol, ul
{
  padding: 0;
  list-style: none;
}

p
{
  line-height: 180%;
}

strong
{
}

a
{
  color: #ee0000;
}

a:hover
{
  text-decoration: none;
  background-color: black;
  border-radius: 4px;
  font-weight: bold;
}

.container
{
  overflow: hidden;
  margin: 0em auto;
  width: 1200px;
}

/*********************************************************************************/
/* Image Style                                                                   */
/*********************************************************************************/

.image
{
  display: inline-block;
}

.image img
{
  display: block;
  width: 100%;
}

.image-full
{
  display: block;
  width: 100%;
  margin: 0 0 2em 0;
}

.image-left
{
  float: left;
  margin: 0 2em 2em 0;
}

.image-centered
{
  display: block;
  margin: 0 0 2em 0;
}

.image-centered img
{
  margin: 0 auto;
  width: auto;
}

/*********************************************************************************/
/* List Styles                                                                   */
/*********************************************************************************/

ul.style1
{
}

ul.actions
{
}

ul.actions li
{
  display: inline-block;
  padding: 0em 0.50em;
}


/*********************************************************************************/
/* Social Icon Styles                                                            */
/*********************************************************************************/

ul.contact
{
  margin: 0;
  padding: 2em 0em 0em 0em;
  list-style: none;
}

ul.contact li
{
  display: inline-block;
  padding: 0em 0.30em;
  font-size: 1em;
}

ul.contact li span
{
  display: none;
  margin: 0;
  padding: 0;
}

ul.contact li a
{
  color: #FFF;
}

ul.contact li a:before
{
  display: inline-block;
  background: #3f3f3f;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  text-align: center;
  color: #FFFFFF;
}

ul.contact li a.icon-twitter:before { background: #2DAAE4; }
ul.contact li a.icon-facebook:before { background: #39599F; }
ul.contact li a.icon-dribbble:before { background: #C4376B;	}
ul.contact li a.icon-tumblr:before { background: #31516A; }
ul.contact li a.icon-rss:before { background: #F2600B; }

/*********************************************************************************/
/* Button Style                                                                  */
/*********************************************************************************/

.button
{
  display: inline-block;
  padding: 1em 2em 1em 2em;
  letter-spacing: 0.10em;
  margin-top: 2em;
  padding: 1.40em 3em;
  background: #000002;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 700;
  font-size: 1em;
  color: #FFF;
}

.button-alt
{
  padding: .5em;
  border-color: rgba(0,0,0,.8);
  color: #FFF;
}
  
/*********************************************************************************/
/* 4-column                                                                      */
/*********************************************************************************/

.box1,
.box2,
.box3,
.box4
{
  width: 235px;
}

.box1,
.box2,
.box3,
{
  float: left;
  margin-right: 20px;
}

.box4
{
  float: right;
}

/*********************************************************************************/
/* 3-column                                                                      */
/*********************************************************************************/

.boxA,
.boxB,
.boxC
{
  width: 384px;
}

.boxA,
.boxB
{
  float: left;
  margin-right: 20px;
}

.boxC
{
  float: right;
}

/*********************************************************************************/
/* 2-column                                                                      */
/*********************************************************************************/

.tbox1,
.tbox2
{
  width: 575px;
}

.tbox1
{
  float: left;
}

.tbox2
{
  float: right;
}


/*********************************************************************************/
/* Heading Titles                                                                */
/*********************************************************************************/

.title
{
  margin-bottom: 3em;
}

.title h2
{
  font-size: 2.7em;
}

.title .byline
{
  font-size: 1.3em;
  color: rgba(255,255,255,0.60);
}

/*********************************************************************************/
/* Header                                                                        */
/*********************************************************************************/

#wrapper
{
  position: relative;
  padding: 5em 0em 7em 0em;
  background: #FFF;
}

/*********************************************************************************/
/* Header                                                                        */
/*********************************************************************************/

#header-wrapper
{
  position: relative;
  padding: 5em 0em 7em 0em;
  background: #111111 url(/notBuggySkipe_front-end/static/media/unsplash2.cc647efc.jpg) no-repeat center;
  background-size: cover;
}

#header
{
  position: relative;
  padding: 5em 0em;
}

/*********************************************************************************/
/* Logo                                                                          */
/*********************************************************************************/

#logo
{
  position: absolute;
  top: 3.6em;
  left: 0;
}

#logo h1
{
  font-size: 2.6em;
  color: #79C255;
}

#logo a
{
  text-decoration: none;
  color: rgba(255,255,255,1);
}

/*********************************************************************************/
/* Menu                                                                          */
/*********************************************************************************/

#menu
{
  position: absolute;
  top: 4.5em;
  right: 0;
}

#menu ul
{
  display: inline-block;
}

#menu li
{
  display: block;
  float: left;
  text-align: center;
}

#menu li a, #menu li span
{
  padding: 1em 1.5em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1em;
  font-weight: bold;
  color: rgba(255,255,255,0.80);
}

#menu li:hover a, #menu li.active a, #menu li.active span
{
}

#menu .active a
{
  background: #2651a8;
  border-radius: 6px;
  color: rgba(255,255,255,1);
}

#menu .icon
{
}

/*********************************************************************************/
/* Banner                                                                        */
/*********************************************************************************/

#banner
{
  padding-top: 10em;
  text-align: center;
}

#banner .title h2
{
  font-size: 4em;
}

#banner .title .byline
{
  font-size: 2em;
}

#banner .button
{
  margin-top: 2em;
  padding: 1.40em 3em;
  border-radius: 6px;
  font-weight: 700;
  font-size: 1em;
  
}

.banner
{
  margin-bottom: 5em;
}

/*********************************************************************************/
/* Page                                                                          */
/*********************************************************************************/

#page
{
}

/*********************************************************************************/
/* Content                                                                       */
/*********************************************************************************/

#content
{
}

/*********************************************************************************/
/* Sidebar                                                                       */
/*********************************************************************************/

#sidebar
{
}

/*********************************************************************************/
/* Footer                                                                        */
/*********************************************************************************/

#footer
{
}

/*********************************************************************************/
/* Copyright                                                                     */
/*********************************************************************************/

#copyright
{
  overflow: hidden;
  padding: 5em 0em;
  border-top: 1px solid rgba(0,0,0,0.08);
}

#copyright p
{
  text-align: center;
  font-size: 1em;
  color: rgba(255,255,255,0.3);
}

#copyright a
{
  text-decoration: none;
  color: rgba(255,255,255,0.6);
}


/*********************************************************************************/
/* Welcome                                                                       */
/*********************************************************************************/

#welcome
{
  position: relative;
  padding: 7em 0em 7em 0em;
  background: #2651a8;
  background-size: cover;
  text-align: center;
}

#welcome .container
{
  width: 1000px;
  padding: 0px 100px;
  color: rgba(255,255,255,0.8);
}

#welcome a
{
  color: rgba(255,255,255,1);
}

#welcome .button
{
  background: #FFF;
  color: #2651a8;
}


/*********************************************************************************/
/* Extra                                                                         */
/*********************************************************************************/

#three-column
{
  text-align: center;
  color: rgba(0,0,0,0.6);
}

#three-column .fa
{
  display: block;
  padding: 1em 0em;
  color: rgba(0,0,0,1);
  font-size: 2em;
}

#three-column .title h2
{
  font-weight: bold;
  color: rgba(0,0,0,0.8);
}

#three-column .title .byline
{
  text-align: center;
  color: rgba(0,0,0,.5);
}

div.edit-profile-tile {
  background-color: rgb(180, 241, 239);
  background-position: 10px 10px;
  background-repeat: no-repeat;
  padding-left: 40px;
  border-radius: 10px;
}

.login-form {
    font-weight: bold;
    padding: 2em 2em;
    display: inline-block;
    vertical-align: middle;
    height: 50px;
}

.login-form input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.signup-form {
  font-weight: bold;
  padding: 2em 2em;
  display: inline-block;
  vertical-align: middle;
  width: 200px;
}

.signup-form input {
  width: 1000px;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.about-tile {
  background-color: white;
  height: 1000px;
  color: black;
  padding: 2em 2em;
}

.contact-us-tile {
  background-color: white;
  height: 1000px;
  color: black;
  padding: 2em 2em;
}

.intro-button {
  background-color: white;
  color: black;
}

.add {
  background-color:#5f8ab8;
  border-radius: 4px;
  color: white;
  height: 30px;
}

.delete {
  background-color: grey;
  border-radius: 4px;
  color: white;
  height: 30px;
}

.view {
  border-radius: 4px;
  height: 30px;
  margin: 10px;
}
